.film {
    width: 1200px;
    margin: auto;
}

.film__text {
    color: white;
}

.film__container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.film__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 1180px;
}

.film__item {
    width: 150px;
    height: 200px;
    background-color: rgba(255, 255, 255, 0.5);
    margin-right: 10px;
    margin-bottom: 10px;
}

.film__image {
    width: 150px;
    height: 150px;
    overflow-y: hidden;
}

.film__item-text {
    font-size: 13px;
    font-weight: 400;
    color: rgb(136, 14, 14);
    text-align: center;
}

.film__field {
    width: 300px;
    height: 320px;
    background-color: rgba(255, 255, 255, 0.5);
    margin-bottom: 10px;
}

.film__button {
    border: none;
    padding-top: 20px;
    width: 145px;
    height: 60px;
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.8);
    text-transform: uppercase;
}

.film__button--first {
    margin-right: 10px;
}



